import './Home.css';
import SideMenu from './SideMenu';
import UltimateTable from './UltimateTable';
import React, { useState, useEffect } from 'react';
import { TableRow } from '@mui/material';
import axios from 'axios';

function Manager() {
  const [category, setCategory] = useState('Orders');
  const [tableData, setTableData] = useState([]);
  const [error, setError] = useState(null);

  const sampleData = [["Dogs", "Cats", "Polar Bears", "Dolphin"], ["1", "2", "3", "13"], ["4", "5", "7", "27"]];

  const sampleData2 = [["llama", "fox", "snow fox"], ["449", "234", "99"], ["22", "29", "123"]];

  const categories = ['Orders', "Inventory", "Products", "Menu", "Customers", "Product Usage", "Sales Report", "Excess Report", "Restock Inventory Report", "Restock Product Report", "Trend Report"];

  // useEffect(() => {
  //     axios.get('/restock')
  //       .then((response) => {
  //         setTableData(response.data);
  //       })
  //       .catch((e) => {
  //         setError(e);
  //   });
  // }, []);


  useEffect(() => {
    if (category === 'Restock Inventory Report') {
      axios.get('/restock?table=inventory')
        .then((response) => {
          console.log("Fetched Data: ", response.data)
          setTableData(response.data);
        })
        .catch((error) => {
          console.error("Error fetching data: ", error);
          setError(error);
        });
    }
  }, [category]);
  function DisplayContent() {

    switch (category) {
      case 'Orders':
        return (
          <UltimateTable data={sampleData}></UltimateTable>
        );

      case 'Inventory':
        return (
          <UltimateTable data={sampleData2}></UltimateTable>
        );
      case 'Products':
        return (
          <UltimateTable data={sampleData}></UltimateTable>
        );

      case 'Menu':
        return (
          <UltimateTable data={sampleData2}></UltimateTable>
        );

      case 'Customers':
        return (
          <p>
            Customers
          </p>
        );

      case 'Product Usage':
        return (
          <p>
            Product Usage
          </p>
        );
      case 'Sales Report':
        return (
          <p>
            Sales Report
          </p>
        );
      case 'Excess Report':
        return (
          <p>
            Excess Report
          </p>
        );
      case 'Restock Inventory Report':
        // console.log("Table Data: ", tableData);
        // if (tableData) {
        //   return <UltimateTable data={tableData} />;
        // } else {
        //   return <div>Loading...</div>;
        // }
        return (
          <p>
            Restock Product Report
          </p>
        );

      case 'Restock Product Report':
        return (
          <p>
            Restock Product Report
          </p>
        );

      case 'Trend Report':
        return (
          <p>
            Trend Report
          </p>
        );
    }

  }

  return (
    <div className="App">
      <header className="App-header">
        <SideMenu categories={categories} categorySwap={setCategory}></SideMenu>
        <button className='managerButtons'>Change to Cashier Interface</button>
        <h3>Manager: {category} </h3>
        <TableRow>
          <button className='managerButtons'>Add Entry</button>
          <button className='managerButtons'>Update Entry</button>
          <button className='managerButtons'>Delete Entry</button>
          <button className='managerButtons'>Seasonal Menu Item</button>
        </TableRow>
        <div className='contentBox'>
          <DisplayContent></DisplayContent>
        </div>
      </header>
    </div>
  );
}

export default Manager;
