import './Home.css';
import logo from './images/tiffsLogo.png';
import SideMenu from './SideMenu';
import React, { useState } from 'react';


function AboutUs() {

  const [category, setCategory] = useState('About Us');

  const categories = ['About Us', 'Hours', 'Contact Information'];

  function DisplayContent(){

      switch(category){
        case 'About Us':
          return(
            <p>
              Tiff's Treats was founded by Tiffany Taylor and Leon Chen in 1999. Born and cultivated in Austin, Texas, we are committed to filling the world with delicious confections.
            </p>
          );

        case 'Hours':
          return(
            <table>
              <tr>
                <p>Monday	9 AM–12 AM</p>
              </tr>
              <tr>
                <p>Tuesday	9 AM–12 AM</p>
              </tr>
              <tr>
                <p>Wednesday	9 AM–12 AM</p>
              </tr>
              <tr>
                <p>Thursday	9 AM–12 AM</p>
              </tr>
              <tr>
                <p>Friday	9 AM–12 AM</p>
              </tr>
              <tr>
                <p>Saturday	10 AM–12 AM</p>
              </tr>
              <tr>
                <p>Sunday	10 AM–12 AM</p>
              </tr>
            </table>
          );

        case 'Contact Information':
          return(
            <table>
              <tr>
                <p>Phone: (512) 473-2600</p>
              </tr>
              <tr>
                <p>Email: customerservice@cookiedelivery.com</p>
              </tr>
              <tr>
                <p>Address: Tiff's Treats Customer Service
                  8310-1 N. Capital of Texas Highway, Suite 110
                  Austin, TX 78731</p>
              </tr>
            </table>
          );
      }

  }
  
  return (
    <div className="App">
      <header className="App-header">
        <SideMenu categories={categories} categorySwap={setCategory}></SideMenu>
        <img className="logo" src={logo} alt="Tiff's Treats Logo. Displays the text: Tiff's Treats Cookie Delivery" />
        <h3>About Tiff's Treats: {category} </h3>
        <div className='contentBox'>
          <DisplayContent></DisplayContent>
        </div>
      </header>
    </div>
  );
}

export default AboutUs;
