import React from 'react';
import './Home.css';

function UltimateTable({data}){

    if(!data){
        return(<p>Error no data</p>);
    }

    const headersList = data[0];

    return (
      <table>
        <thead>
            <tr className='ultimateTable'>
                {headersList.map((header, i) => (
                <th className='ultimateTable' key={i}>{header}</th>
                ))}
            </tr>
        </thead>

        <tbody>
            {data.slice(1).map((row, i) => (
            <tr className='ultimateTable' key={i}>
                {row.map((entry, j) => (
                <td  className='ultimateTable'key={j}>{entry}</td>
                ))}
            </tr>
            ))}
        </tbody>
      </table>
    );
}

export default UltimateTable;