import SideMenu from './SideMenu';
import React, { useState } from 'react';

function Menu() {

    const [category, setCategory] = useState('Cookies');

    //this needs to be an api call to get categories
    //will also need api call to get things in category when displaying them on menu.
    const categories = ['Cookies', 'Brownies', 'Ice Cream', 'Beverages'];

    return (
      <div className="App">
        <header className="App-header">
        <SideMenu categories={categories} categorySwap={setCategory}></SideMenu>
        <div className="content-box">
          <p>{category}</p> 
        </div>
        </header>
      </div>
    );
  }
  
  export default Menu;